import React from 'react'
import {Nav, NavItem} from 'react-bootstrap'
import {LinkContainer} from 'react-router-bootstrap'
import './Form.scss'
export default class SettingsLayout extends React.Component {
  render () {
    return (
      <div>
        <div className='page-header'>
          <h1></h1>
        </div>
        <div className='row'>
          <div className='col-sm-3 col-md-3'>
            <Nav bsStyle='pills' stacked activeKey={1}>
              <LinkContainer to={{pathname: '/settings/profile'}}>
                <NavItem eventKey={1} title=' '> </NavItem>
              </LinkContainer>
              <LinkContainer to={{pathname: '/settings/avatar'}}>
                <NavItem eventKey={2} title=' '> </NavItem>
              </LinkContainer>
              <LinkContainer to={{pathname: '/settings/password'}}>
                <NavItem eventKey={3} title=' '> </NavItem>
              </LinkContainer>
              <NavItem eventKey={4} title=''></NavItem>
            </Nav>
          </div>
          <div className='col-sm-9 col-md-8'>
              {this.props.children}
          </div>
        </div>
      </div>
    )
  }
}
SettingsLayout.propTypes = {
  children: React.PropTypes.element.isRequired
}



