import React from 'react'

const ContentBox = ({children}) => (
  <div className='row content-boxes margin-bottom-20'>
    {children}
  </div>
)
const BoxItem = ({children, text}) => (
  <div className='col-md-4 md-margin-bottom-20'>
    <h4 className='heading-sm'>
      {children}
    </h4>
    <p className='text-justify'>{text}</p>
  </div>
)
export const IconRow = () => (
  <div className='margin-top-30'>
    <ContentBox>
      <BoxItem text=' -       .'>
        <a href='#'>
          <i className='icon-custom fa fa-thumbs-o-up'></i>
          <span> /</span>
        </a>
      </BoxItem>
      <BoxItem text='  10-15       .'>
        <a href='#'>
          <i className='icon-custom fa fa-thumbs-o-up'></i>
          <span>  </span>
        </a>
      </BoxItem>
      <BoxItem text='     .    . '>
        <a href='#'>
          <i className='icon-custom fa fa-thumbs-o-up'></i>
          <span>    </span>
        </a>
      </BoxItem>
    </ContentBox>
    <ContentBox>
      <BoxItem text=' -   800 ..'>
        <a href='#'>
          <i className='icon-custom fa fa-thumbs-o-up'></i>
          <span>     </span>
        </a>
      </BoxItem>
      <BoxItem text='       .'>
        <a href='#'>
          <i className='icon-custom fa fa-thumbs-o-up'></i>
          <span>    </span>
        </a>
      </BoxItem>
      <BoxItem text='   .'>
        <a href='#'>
          <i className='icon-custom fa fa-thumbs-o-up'></i>
          <span></span>
        </a>
      </BoxItem>
    </ContentBox>
  </div>
)
export default IconRow



